import React from 'react';
import classnames from 'classnames';
import { DesWrap, GalleryButton, TextWrap, TitleWrap } from './style';

export interface ITextTypeSelectOption {
  title: string;
  type: string;
  default: number;
  fontWeight: number;
  lineHeight: number | string;
  des: string;
}

export interface FontSizeButtonProps {
  option: ITextTypeSelectOption;
  value?: string;
  minFontSize: number;
  maxFontSize: number;
  defaultFontFamily: string;
}

const FontSizeButton: React.FC<FontSizeButtonProps> = ({
  option,
  value,
  minFontSize,
  maxFontSize,
  defaultFontFamily,
}) => {
  return (
    <GalleryButton
      key={option.type}
      value={option.type}
      testid={option.type}
      active={option.type === value}
      className={classnames({
        active: option.type === value,
      })}
    >
      <TitleWrap
        type={option.type}
        style={{
          fontSize: `${Math.min(
            Math.max(option.default * 0.75, minFontSize),
            maxFontSize,
          )}pt`,
          fontWeight: option.fontWeight,
          lineHeight: '29px',
          height: '29px',
          textOverflow: 'clip',
          maxWidth: '65px',
          overflow: 'hidden',
        }}
      >
        {option.title}
      </TitleWrap>
      <DesWrap>
        <TextWrap>
          {option.title === 'AaBbCcDdEe'
            ? option.des
            : `${defaultFontFamily} ${
                option.type === 'blockquote' ? '-' : option.default
              }`}
        </TextWrap>
      </DesWrap>
    </GalleryButton>
  );
};

export default FontSizeButton;
